___CSS_LOADER_EXPORT___.push([module.id, `body {
    padding: 0;
    margin: 0;
    color: white;
    background-color: #000;
    font-family: 'Play', sans-serif;
    font-size: 100%;
}

#aisa {
    cursor: zoom-in;
}

/* debugging */
#debug {
    bottom: 0;
    height: 24px;
    width: 640px;
    background-color: black;
    display: none;
}

#ticker_controls {
    padding: 10px 0 10px 0;
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
}

#ticker_controls .fa {
    cursor: pointer;
    color: white;
    padding-left: 8px;
    padding-right: 8px;
    width: 16px;
}

#timeline {
    -webkit-appearance: none;
    width: 100%;
    height: 25px;
    background: #fff;
    outline: none;
    -webkit-transition: .2s;
    transition: opacity .2s;
}

#timeline::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 20px;
    height: 25px;
    background: rgb(10, 28, 109);
    cursor: pointer;
}

#timeline::-moz-range-thumb {
    width: 20px;
    height: 25px;
    background: #000;
    cursor: pointer;
}

#scene, #time {
    font-weight: bold;
    padding-right: 10px;
}
